/***/ (function(module, exports) {

	module.exports = {"data":{"site":{"siteMetadata":{"title":"MAC0215 Blog","author":"Victor Hugo M. Pinto"}},"markdownRemark":{"id":"/Users/victorhugo/BCC/mac0215-blog/src/pages/padronização-de-campos-IV/index.md absPath of file >>> MarkdownRemark","html":"<p>Durante esta semana (30 de setembro - 06 de outubro), avançamos novamente com a padronização dos dados obtidos nas planilhas com informações sobre os inscritos.</p>\n<p>Continuando o trabalho iniciado na última semana, trabalhamos na anonimização dos dados dos inscritos e a padronização dos últimos campos não-estruturados.</p>\n<p>Para todas as planilhas de inscritos foram feitas as seguintes alterações:</p>\n<ul>\n<li>Removida a coluna <code class=\"language-text\">Em que nível de estudo você está atualmente?</code>, juntando seus dados com os fornecidos na pergunta <code class=\"language-text\">Em que ano você está?</code>, assumindo que, para níveis superiores à graduação, o ano perde a sua importância;</li>\n<li>Criação de uma nova coluna <code class=\"language-text\">Selecionado</code>, preenchida com <code class=\"language-text\">S</code> (sim) ou <code class=\"language-text\">N</code> (não) de acordo com as planilhas de selecionados de cada edição.</li>\n</ul>\n<p>Para todas as planilhas de inscritos, com excessão da edição 2016.1 foram feitas as seguintes alterações:</p>\n<ul>\n<li>Removidas as entradas para o campo <code class=\"language-text\">Nome Completo</code>, assim retirando o nome fornecido por cada inscrito;</li>\n<li>Removidas as entradas para o campo <code class=\"language-text\">Endereço de e-mail</code>, retirando os e-mails dos inscritos;</li>\n<li>Removidos os nomes fornecidos no campo <code class=\"language-text\">Se sim, conte-nos quem são</code>, onde os inscritos forneciam os nomes dos integrantes de suas equipes. Os nomes foram substituídos pelos seus respectivos IDs, únicos para cada inscrito em uma edição, sendo a parte mais demorada do trabalho feito;</li>\n<li>Padronização, da melhor maneira possível com base nos dados fornecidos, do campo <code class=\"language-text\">Qual a sua especialidade?</code> buscando agrupar respostas com o mesmo significado sob uma grafia comum para possibilitar melhor análise;</li>\n<li>Padronização, da melhor maneira possível com base nos dados fornecidos, do campo <code class=\"language-text\">Qual o seu curso / programa de pós?</code> buscando agrupar respostas equivalentes (representando o mesmo curso) sob uma mesma grafia, para possibilitar melhor análise futura;</li>\n</ul>\n<p>Além disso, na planilha de inscritos da edição 2017.2 também foi padronizado com o objetivo de agrupar respostas com o mesmo significado sob uma grafia comum o campo <code class=\"language-text\">Em que universidade você estuda (ou estudou)?</code>.</p>\n<p>A planilha da edição 2016.1 está “atrasada” em relação às demais devido ao seu número de linhas (inscritos) muito superior. Durante a próxima semana ela será tratada com maior atenção.</p>\n<p><strong>Horas</strong>: 8 horas</p>","frontmatter":{"title":"Padronização de campos (Pt. 4)","date":"October 06, 2018"}}},"pathContext":{"slug":"/padronização-de-campos-IV/","previous":{"fields":{"slug":"/padronização-de-campos-III/"},"frontmatter":{"title":"Padronização de campos (Pt. 3)"}},"next":{"fields":{"slug":"/padronização-de-campos-V/"},"frontmatter":{"title":"Padronização de campos (Pt. 5)"}}}}

/***/ })

